import React, {Component} from "react"
import Timer from "./Timer"
import EditInterface from "./EditInterface"
import {generateColours, millisecToMinSec} from "./helpers"
import "./App.css"

const endSound = new Audio("endSound.mp3")

const Shake = require("shake.js")
const myShakeEvent = new Shake({
    threshold: 10, // optional shake strength threshold
    timeout: 300, // optional, determines the frequency of event generation
})
myShakeEvent.start()

class App extends Component {
    constructor() {
        super()
        this.start = this.start.bind(this)
        this.stop = this.stop.bind(this)
        this.pause = this.pause.bind(this)
        this.newExercise = this.newExercise.bind(this)
        this.updateCaption = this.updateCaption.bind(this)
        this.updateMinutes = this.updateMinutes.bind(this)
        this.updateSeconds = this.updateSeconds.bind(this)
        this.startTimer = this.startTimer.bind(this)

        window.addEventListener("shake", this.stop, false)

        let exercises = []

        this.state = {
            playing: "STOPPED",
            interval: null,
            minutes: 0,
            seconds: 0,
            totalMilliSec: 0,
            previousMillisec: 0,
            exercises,
            colours: generateColours(exercises.length),
            currentExercise: 0,
            pauseAfter: true,
            editing: true,
        }

        window.onpopstate = e => {
            this.setState(state => {
                clearInterval(state.interval)
                return {
                    editing: true,
                    playing: "STOPPED",
                    interval: null,
                    minutes: 0,
                    seconds: 0,
                    totalMilliSec: 0,
                    previousMillisec: 0,
                    currentExercise: 0,
                }
            })
        }
    }

    start() {
        endSound.muted = true
        endSound.play()

        this.startDate = new Date()
        const interval = setInterval(() => {
            const {
                currentExercise,
                exercises,
                previousMillisec,
                interval,
                pauseAfter,
            } = this.state
            let totalMilliSec = new Date() - this.startDate + previousMillisec

            if (totalMilliSec <= exercises[currentExercise].time) {
                this.setState({
                    ...millisecToMinSec(totalMilliSec),
                    totalMilliSec,
                })
            } else if (currentExercise < exercises.length - 1) {
                endSound.muted = false
                endSound.play()

                if (pauseAfter) {
                    clearInterval(this.state.interval)

                    this.setState((state, props) => ({
                        playing: "PAUSED",
                        currentExercise: state.currentExercise + 1,
                        totalMilliSec: 0,
                        previousMillisec: 0,
                        interval: null,
                        ...millisecToMinSec(
                            state.exercises[state.currentExercise].time
                        ),
                    }))
                } else {
                    this.startDate = new Date()
                    this.setState((state, props) => ({
                        currentExercise: state.currentExercise + 1,
                        totalMilliSec: 0,
                        previousMillisec: 0,
                        colours: generateColours(state.exercises.length),
                    }))
                }
            } else {
                endSound.muted = false
                endSound.play()

                clearInterval(interval)
                this.setState((state, props) => ({
                    playing: "STOPPED",
                    previousMillisec: 0,
                    interval: null,
                    ...millisecToMinSec(
                        state.exercises[state.currentExercise].time
                    ),
                    totalMilliSec,
                }))
            }
        }, 10)

        this.setState((state, props) => {
            if (state.playing === "PAUSED") {
                return {playing: "PLAYING", interval}
            } else {
                return {
                    playing: "PLAYING",
                    currentExercise: 0,
                    interval,
                }
            }
        })
    }

    pause() {
        clearInterval(this.state.interval)
        this.setState((state, props) => ({
            playing: "PAUSED",
            previousMillisec: state.totalMilliSec,
            interval: null,
        }))
    }

    stop() {
        clearInterval(this.state.interval)
        this.setState((state, props) => ({
            playing: "STOPPED",
            previousMillisec: 0,
            minutes: 0,
            seconds: 0,
            totalMilliSec: 0,
            currentExercise: 0,
            interval: null,
            colours: generateColours(state.exercises.length),
        }))
    }

    newExercise() {
        this.setState(state => ({
            exercises: [...state.exercises, {time: 0, caption: ""}],
        }))
    }

    updateCaption(e, targetId) {
        let targetCaption = e.target.value
        this.setState(state => ({
            exercises: state.exercises.map(
                (exercise, id) =>
                    id === targetId
                        ? {...exercise, caption: targetCaption}
                        : exercise
            ),
        }))
    }

    updateMinutes(e, targetId) {
        let targetMinutes = e.target.value
        targetMinutes =
            targetMinutes > 59 ? 0 : targetMinutes < 0 ? 59 : targetMinutes

        this.setState(state => ({
            exercises: state.exercises.map((exercise, id) => {
                const {seconds, minutes} = millisecToMinSec(exercise.time)
                const newTime = seconds * 1000 + targetMinutes * 60000

                return id === targetId ? {...exercise, time: newTime} : exercise
            }),
        }))
    }

    updateSeconds(e, targetId) {
        let targetSeconds = e.target.value

        this.setState(state => ({
            exercises: state.exercises.map((exercise, id) => {
                if (id !== targetId) return exercise

                let {seconds, minutes} = millisecToMinSec(exercise.time)

                if (targetSeconds > 59) {
                    console.log("UP")
                    if (minutes < 59) {
                        targetSeconds = 0
                        minutes++
                    } else {
                        targetSeconds = 59
                    }
                } else if (targetSeconds < 0) {
                    if (minutes > 0) {
                        targetSeconds = 59
                        minutes--
                    } else {
                        targetSeconds = 0
                    }
                }

                const newTime = targetSeconds * 1000 + minutes * 60000

                return {...exercise, time: newTime}
            }),
        }))
    }

    startTimer() {
        this.setState({editing: false})
        window.history.pushState({}, "Timer", "runTimer")
    }

    render() {
        let {
            minutes,
            seconds,
            playing,
            totalMilliSec,
            exercises,
            currentExercise,
            colours,
            editing,
        } = this.state

        let percent, firstColor, secondColor, timeStamp, caption

        if (!editing) {
            percent =
                totalMilliSec / exercises[currentExercise].time * 100 + "%"

            firstColor = colours[currentExercise + 1]
            secondColor = colours[currentExercise]
            timeStamp = `${minutes}:${seconds}`
            caption = exercises[currentExercise].caption
        }

        return (
            <div className="App">
                {editing ? (
                    <EditInterface
                        newExercise={this.newExercise}
                        exercises={exercises}
                        updateCaption={this.updateCaption}
                        updateMinutes={this.updateMinutes}
                        updateSeconds={this.updateSeconds}
                        startTimer={this.startTimer}
                    />
                ) : (
                    <Timer
                        playing={playing}
                        timeStamp={timeStamp}
                        percent={percent}
                        firstColor={firstColor}
                        secondColor={secondColor}
                        pause={this.pause}
                        start={this.start}
                        stop={this.stop}
                        caption={caption}
                    />
                )}
            </div>
        )
    }
}

export default App



