import React from "react"
import {millisecToMinSec} from "./helpers"

const EditInterface = ({
    newExercise,
    exercises,
    updateCaption,
    updateMinutes,
    updateSeconds,
    startTimer,
}) => (
    <div>
        {exercises.map((exercise, id) => {
            let {seconds, minutes} = millisecToMinSec(exercise.time)
            seconds = seconds > 9 ? seconds.toString() : "0" + seconds
            minutes = minutes > 9 ? minutes.toString() : "0" + minutes

            return (
                <div key={id}>
                    <input
                        type="text"
                        value={exercise.caption}
                        placeholder="Exercise name"
                        onChange={e => updateCaption(e, id)}
                    />
                    &nbsp;&nbsp;
                    <input
                        id="inputMinutes"
                        type="number"
                        min="-10"
                        max="70"
                        value={minutes}
                        size={2}
                        onChange={e => updateMinutes(e, id)}
                    />
                    :
                    <input
                        id="inputSeconds"
                        type="number"
                        min="-10"
                        max="70"
                        value={seconds}
                        size={2}
                        onChange={e => updateSeconds(e, id)}
                    />
                </div>
            )
        })}
        <button onClick={newExercise}>ADD NEW EXERCISE</button>
        <br />
        <button onClick={startTimer} disabled={exercises.length === 0}>
            DONE
        </button>
    </div>
)

export default EditInterface



