import randomColor from "randomcolor"
import * as chromatism from "chromatism"

function generateColours(n) {
    let colours = []

    while (colours.length <= n) {
        const colour = randomColor({luminosity: "dark"}) // a hex code for an attractive color

        colours = [...colours, ...chromatism.uniformTetrad(colour).hex]
    }

    return colours
}

function millisecToMinSec(remainder) {
    const minutes = Math.floor(remainder / 60000)
    remainder = remainder % 60000
    const seconds = Math.floor(remainder / 1000)
    remainder = remainder % 1000

    return {minutes, seconds}
}

export {generateColours, millisecToMinSec}



