import React from "react"

const Timer = ({
    playing,
    timeStamp,
    percent,
    firstColor,
    secondColor,
    pause,
    start,
    stop,
    caption,
}) => (
    <svg
        width="100%"
        height="100%"
        xmlns="http://www.w3.org/2000/svg"
        onWheel={stop}
        onClick={playing === "PLAYING" ? pause : start}
    >
        <defs key={timeStamp}>
            <clipPath id="cliPath">
                <text
                    key={timeStamp}
                    id="timeText"
                    x="50%"
                    textAnchor="middle"
                    y="40%"
                >
                    {timeStamp}
                </text>
                <text
                    key={caption}
                    id="caption"
                    x="50%"
                    textAnchor="middle"
                    y="60%"
                >
                    {caption}
                </text>
            </clipPath>
            <linearGradient id="Gradient1" x1="0" x2="1" y1="0" y2="0">
                <stop offset={percent} stopColor={firstColor} />
                <stop offset={percent} stopColor={secondColor} />
            </linearGradient>
            <linearGradient id="Gradient2" x1="0" x2="1" y1="0" y2="0">
                <stop offset={percent} stopColor={secondColor} />
                <stop offset={percent} stopColor={firstColor} />
            </linearGradient>
        </defs>
        <rect x="0" y="0" width="100%" height="100%" fill="url(#Gradient1)" />
        <rect
            x="0"
            y="0"
            width="100%"
            height="100%"
            clipPath="url(#cliPath)"
            fill="url(#Gradient2)"
        />
    </svg>
)

export default Timer



